.btn {
    &,
    &:link,
    &:visited {
        text-decoration: none;
        text-transform: uppercase;
        padding: 1.5rem 4rem;
        display: inline-block;
        border-radius: 10rem;
        transition: transform 0.2s;
        position: relative;


        //compat for button
        border: none;
        cursor: pointer;
        transition: all .2s;
    }


    &:hover {
        transform: translateY(-0.3rem);
        box-shadow: 0 0.5rem 1rem rgba($color-black, 0.3);

        &::after {
            transform: scaleX(1.4) scaleY(1.6);
            opacity: 0;
        }
    }

    &::after {
        content: "";
        display: inline-block;
        width: 100%;
        height: 100%;
        border-radius: 10rem;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        transition: all 0.4s;
    }


    
    &--animated {
        animation: moveUp 1s;
        animation-delay: 0.5s;
        animation-fill-mode: backwards;
    }

    &--white {
        color: $color-gray;
        background-color: $color-white;

        &::after {
            background-color: $color-white;
        }
    }

    &--gray {
        color: $color-white;
        background-color: $color-gray;

        &::after {
            background-color: $color-gray;
        }
    }

    &--green {
        color: $color-white;
        background-color: $primary-color;

        &::after {
            background-color: $primary-color;
        }
    }

    
    &:active,
    &:active {
        transform: translateY(-1px);
        box-shadow: 0 .5rem 1rem rgba($color-black, .4);
        outline: none;
    }
    
}

.btn-text {
    &:link, &:visited {
        color: $primary-color;
        display: inline-block;
        text-decoration: none;
        border-bottom: 1px solid $primary-color;
        padding: .3rem;
        font-size: $default-font-size;
        transition: all 0.2s;
    }

    &:hover {
        border-bottom: 0px;
        transform: translateY(-5px);
        background-color: $primary-color;
        color: $color-white;
        box-shadow: 0 1rem 3rem rgba($color-black, 0.3);
        
    }

    &:active {
        transform: translateY(-2px);
        box-shadow: 0 0.5rem 3rem rgba($color-black, 0.3);
    }
}