.footer {
    background-color: $color-gray-dark;
    padding: 10rem 0;
    font-size: 1.4rem;

    @include respond-phone(tab-port) {
        padding: 8rem 0;
    }

    @include respond-phone(tab-land) {
        padding: 9rem 0;
    }

    &__logo-box {
        text-align: center;
    }

    &__logo {
        width: 15rem;

        &:hover {
        animation: goRound .8s ease forwards;
        }

    }

    &__navigation {
        border-top: .2rem solid rgba($color-light-grey-1, .3);
        padding-top: 2rem;

        display: inline-block;

        @include respond-phone(tab-port) {
            width: 100%;
            text-align: center;

        }
    }

    &__item{
        display: inline-block;

        &:not(:last-child)
        {
            margin-right: 2rem;
        }
    }  

    &__link{
        &:link, &:visited {
            color: $color-light-grey-1;
            text-decoration: none;
            text-transform: uppercase;
            display: inline-block;
            transition: all 0.2s;
        }

        &:hover, &:active {
            color: $primary-color;
            // box-shadow: 0 1rem 2rem rgba($color-black, 0.3);
        }
    }

    &__copyright {
        color: $primary-color;

        @include respond-phone(tab-port) {
           width: 100%;
        }
    }
}
