.section-about {
    background-color: $color-light-grey-1;
    padding: 25rem 0;
    margin-top: -20vh;

    @include respond-phone(tab-port) {
        padding: 26rem 0;
    }
}

.section-features {
    padding: 20rem 0;
    background-image: linear-gradient(to right bottom, rgba($primary-color-light, 0.8), rgba($primary-color-dark, 0.8)),
        url("../../assets/nat-4.jpg");
    background-size: cover;
    transform: skewY(-7deg);
    margin-top: (-15vh);

    & > * {
        transform: skewY(7deg);
    }

    @include respond-phone(tab-port) {
        padding: 18rem 0;
    }
}

.section-tours {
    background-color: $color-light-grey-1;
    padding: 25rem 0 20rem 0;
    margin-top: -15vh;

    @include respond-phone(tab-port) {
        padding: 24rem 0 15rem 0;
    }
}

.section-stories {
    padding: 15rem 0;
    transform: skewY(-7deg);
    margin-top: -10vh;

    & > * {
        transform: skewY(7deg);
    }
    position: relative;
    @include respond-phone(tab-port) {
        padding: 13rem 0;
        margin-top: -8vh;
    }
}

.section-book {
    padding: 15rem;
    background-image: linear-gradient(to right bottom, $primary-color-light, $primary-color-dark);
    @include respond-phone(tab-port) {
        padding: 10rem 3rem;
    }
}

.book {
    // background-image: url('../../assets/nat-10.jpg');
    background-size: cover;
    height: 65rem;
    border-radius: .3rem;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.3);
    background-image: linear-gradient(105deg,rgba($color-white, .8) 0%, rgba($color-white, .8) 50%, transparent 50%), url('../../assets/nat-10.jpg');

    @include respond-phone(tab-port) {
        background-image: linear-gradient(105deg,rgba($color-white, .8) 0%, rgba($color-white, .8) 60%, transparent 50%), url('../../assets/nat-10.jpg');
    }

    &__form {
        width: 50%;
        padding: 6rem;

        @include respond-phone(tab-port) {
            width: 65%;
            padding: 4.5rem;
        }
    }

    
}