.popup {
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: 30;
    background-color: rgba($color-black, 0.8);

    opacity: 0;
    visibility: hidden;
    transition: all 0.5s;


    &__content {
        @include absCenter;

        width: 75%;
        overflow: hidden;
        border-radius: 3px;
        background-color: $color-white;
        box-shadow: 0 2rem 4rem rgba($color-black, 0.3);
        display: table;
        transform: translate(-50%, -50%) scale(0.25);
        opacity: 0;
        transition: all .5s;
    }

    &__left {
        width: 33.33333%;
        display: table-cell;
    }

    &__right {
        display: table-cell;
        width: 66.66667%;
        vertical-align: middle;
        padding: 3rem 5rem;
    }

    &__img {
        display: block;
        width: 100%;
    }

    &__text {
        font-size: 1.4rem;
        margin-bottom: 4rem;

        -moz-column-count: 2;
        -moz-column-gap: 4rem;
        -moz-column-rule: 1px solid rgba($color-gray, 0.2);

        column-count: 2;
        column-gap: 4rem;
        column-rule: 1px solid rgba($color-gray, 0.2);

        text-align: justify;

        -moz-hyphens: auto;
        -ms-hyphens: auto;
        -webkit-hyphens: auto;
        hyphens: auto;
    }

    
    &:target {
        opacity: 1;
        visibility: visible;
        
    }

    &:target &__content {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }

    &__close {
        &:link,
        &:visited {
            color: $color-gray;
            position: absolute;
            top: 2.5rem;
            right: 2.5rem;
            text-decoration: none;
            font-size: 3rem;
            display: inline-block;
            transition: all .2s;
            line-height: 1;
        }

        &:hover{
            color: $primary-color;
        }
    }
}
