*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    font-size: 62.5%; // defines 1 rem = 10px -> 10/16 -> not typography.
 // we remove phone because our 1 rem should still be 50%

   
    @include respond-phone(tab-land) { //1200
        font-size: 56.25%; // 1 rem = 9px -> 9/16
    }

    @include respond-phone(tab-port) { //900
        // resorted to fix larger before smaller -- defined later = precedence
        font-size: 50%; // 1 rem = 8px -> 8/16
    }


    @include respond-phone(big-desktop) {
        font-size: 75%; // 1 rem = 12px
    }
}


body {
    box-sizing: border-box;
    padding: 2.5rem;
    
    @include respond-phone(tab-port) {
        padding: 0;
    }
}

