@mixin clearfix {
    &::after {
        content: "";
        clear: both;
        display: table;
    }
}

@mixin absCenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

// Media Querie Manager
/* 
0 - 600: Phone - will need MAX width
600 - 900: Portrait Tab - will need MAX width
900 - 1200: Landscape Tab - will need MAX width
1200 - 1800: Desktop ( current setup ) - will need MAX width
1800 - Inf: Large Desktops - will need MIN width

*/
// Order Base + typography > General layout + Grid + Page Layout >  Component
// 1 em -> 16 px

@mixin respond-phone($breakpoint) {
    @if $breakpoint == phone {
        @media (max-width: 37.5em) { // 600px ( 600/16)
            @content;
        }
    }

    @if $breakpoint == tab-port {
        @media (max-width: 56.25em) {  // 900px
            @content;
        }
    }
    @if $breakpoint == tab-land {
        @media (max-width: 75em) { // 1200px
            @content;
        }
    }

    @if $breakpoint == big-desktop {
        @media (min-width: 112.5em) {    // 1800px -> MIN WIDTH
            @content;
        }
    }
}
