@import "../abstract/variables";

body {
    font-family: "Lato", sans-serif;
    font-weight: 400;
    font-size: $default-font-size;
    line-height: 1.7;
    color: $color-gray;
  
}

.heading-primary {
    color: $color-white;
    text-transform: uppercase;
    backface-visibility: hidden;
    margin-bottom: 4rem;

    &--main {
        display: block;
        font-size: 6rem;
        font-weight: 700;
        letter-spacing: 3.5rem;

        &-animated {
            
        animation: moveInLeft 1s;
        animation-timing-function: ease-out;
        }

        @include respond-phone(phone) {
            letter-spacing: 2rem;
            font-size: 5rem;
        }
    
    }

    &--sub {
        display: block;
        font-size: 2rem;
        font-weight: 400;
        letter-spacing: 1.5rem;

        &-animated {

            animation: moveInRight 1s;
            animation-timing-function: ease-out;
        }

        @include respond-phone(phone) {
            letter-spacing: .5rem;
            
        }
    
    }
}

.heading-secondary {
    font-size: 3.5rem;
    text-transform: uppercase;
    font-weight: 800;
    background-image: linear-gradient(to right, $primary-color-light, $primary-color-dark);
    display: inline-block;
    background-clip: initial;
    -webkit-background-clip: text;
    color: transparent;
    transition: all 0.2s;
    letter-spacing: 0.2rem;

    &:hover {
        transform: scale(1.1);
        text-shadow: 0.5rem 1rem 2rem rgba($color-black, 0.2);
    }

    @include respond-phone(tab-port) {
        font-size: 3rem;
    }

    @include respond-phone(phone) {
        font-size: 2.5rem;
        
    }
}

.heading-tertiary {
    font-size: $default-font-size;
    font-weight: 700;
    text-transform: uppercase;
}

.paragraph {
    font-size: $default-font-size;
    text-align: justify;

    &:not(:last-child) {
        margin-bottom: 3rem;
    }

  
}

.text-container {
    [class^="text-container--"]:not(:last-child) {
        display: block;
    }

    padding: 2rem;
    color: $color-white;
    cursor:default;

    & span:not(:last-child) {
        margin-bottom: 3rem;
    }

    &--fn {
        font-size: 3rem;
        letter-spacing: 3rem;
        width: 60%;
        opacity: 0;
        animation: slide-in 0.5s 1.5s forwards ease-in-out;
        transition: all .5s;

        &:hover {
            letter-spacing: 1rem;
        }

        @include respond-phone(tab-port) {
            font-size: 2.5rem;
            letter-spacing: 2rem;
        }

        @include respond-phone(phone) {
            font-size: 2rem;
            letter-spacing: 1.6rem;
        }

    }

    &--mn {
        font-size: 2.6rem;
        letter-spacing: 2.6rem;
        
        width: 40%;
        opacity: 0;
        animation: slide-in 0.5s 2s forwards ease-in-out;
        transition: all .5s;

        &:hover {
            letter-spacing: 1rem;
        }
        @include respond-phone(tab-port) {
            font-size: 2rem;
            letter-spacing: 2rem;
        }

        @include respond-phone(phone) {
            font-size: 1.8rem;
            letter-spacing: 1.4rem;
        }
    }

    &--ln {
        font-size: 2.2rem;
        letter-spacing: 1.6rem;
        display: inline-block;
        width: 30%;
        opacity: 0;
        animation: slide-in 0.5s 2.5s forwards ease-in;
        transition: all .5s;

        &:hover {
            letter-spacing: 1rem;

            @include respond-phone(phone) {
                letter-spacing: .6rem;
            }
        }

        @include respond-phone(tab-port) {
            font-size: 1.8rem;
            letter-spacing: 1.2rem;
        }

        @include respond-phone(phone) {
            font-size: 1.5rem;
            letter-spacing: .8rem;
        }
    }
}

.modal-heading {
    font-size: 5rem;

    @include respond-phone(phone) {
        font-size: 4.5rem;
        text-align: right;
    }
}