.card {

    //ROTATING Functionality
    perspective: 150rem;
    height: 52rem;
    -moz-perspective: 150rem;
    position: relative;

    &__side {
        height: 52rem;
        width: 100%;
        transition: transform 1.5s ease;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 0.3rem;
        overflow: hidden; // hide overflowing image on top
        box-shadow: 0 1.5rem 4rem rgba($color-black, 0.15);
        backface-visibility: hidden;

        &--front {
            background-color: $color-white;
        }

        &--back {
            transform: rotateY(180deg); // rotated around initially

            &-1 {
                background-image: linear-gradient(to right bottom, $secondary-color-light, $secondary-color-dark);
            }

            &-2 {
                background-image: linear-gradient(to right bottom, $primary-color-light, $primary-color-dark);
            }
    
            &-3 {
                background-image: linear-gradient(to right bottom, $tertiary-color-light, $tertiary-color-dark);
            }
        }

        
    }

    &:hover &__side--front {
        transform: rotateY(-180deg);  // rotated around as hovered
    }

    &:hover &__side--back {
        transform: rotateY(0); // brought to front
    }

    // CARD FRONT styling

    &__picture {
        background-size: cover;
        height: 23rem;
        background-blend-mode: screen;
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%,0 100%);
        clip-path: polygon(0 0, 100% 0, 100% 85%,0 100%);
        border-top-left-radius: .3rem;
        border-top-right-radius: .3rem;

        &--1 {
            background-image: linear-gradient(to right bottom, $secondary-color-light, $secondary-color-dark), url('../../assets/nat-5.jpg');
        }

        &--2 {
            background-image: linear-gradient(to right bottom, $primary-color-light, $primary-color-dark),url('../../assets/nat-6.jpg');
        }

        &--3 {
            background-image: linear-gradient(to right bottom, $tertiary-color-light, $tertiary-color-dark),url('../../assets/nat-7.jpg');
        }
    }

    &__heading {
        font-size: 2.8rem;
        font-weight: 300;
        text-transform: uppercase;
        color: $color-white;
        position: absolute;
        right: 2rem;
        top:12rem;
        text-align: right;
        width: 75%;
    }

    &__heading-span {
        padding: 1rem 1.5rem;
        -webkit-box-decoration-break: clone;
        box-decoration-break: clone;

        &--1 {
            background-image: linear-gradient(to right bottom, rgba($secondary-color-light, 0.85),rgba($secondary-color-dark, 0.85));
        }

        &--2 {
            background-image: linear-gradient(to right bottom, rgba($primary-color-light, 0.85),rgba($primary-color-dark, 0.85));
        }

        &--3 {
            background-image: linear-gradient(to right bottom, rgba($tertiary-color-light, 0.85),rgba($tertiary-color-dark, 0.85));
        }

    }

    &__details {
        padding: 2.5rem;
        ul {
            list-style: none;
            width: 80%;
            margin: 0 auto;

            li{
                text-align: center;
                font-size: 1.5rem;
                padding: 1rem;

                &:not(:last-child){
                    border-bottom: 1px solid $color-light-gray-2;
                }
            }
        }
    }

    // CARD BACK Styling

    &__cta{
        @include absCenter;
        width: 90%;
        
        text-align: center;
    }

    &__price-box {
        margin-bottom: 8rem;
        color: $color-white;
    }

    &__price-only {
        font-size: 1.4rem;
        text-transform: uppercase;
    }

    &__price-value {
        font-size: 6rem;
        font-weight: 100;
    }
}
