.intro {
    position: relative;

    &__bg {
        height: 100vh;
        width: 100vw;
        z-index: 30;
        position: fixed;
        top: 0;
        left: 0;
        background-color: rgba($color-black, 0.8);
        padding: 0;
        backface-visibility: hidden;

        &-container {
            @include absCenter;
            width: 60%;
            height: 80%;
            background-image: linear-gradient(
                105deg,
                rgba($color-gray, 1) 20%,
                rgba($color-gray, 0.8) 35%,
                $color-white 35%
            );
            animation: load-box 1.25s ease-in-out;

            

            @include respond-phone(tab-land) {
                width: 85%;
                height: 75%;
                transform: translate(-50%, -50%);
            }

            @include respond-phone(tab-port) {
                width: 90%;
                height: 70%;
                transform: translate(-50%, -50%);
            }

            @include respond-phone(phone) {
                width: 90%;
                height: 80%;
                transform: translate(-50%, -50%);
            }
            
            &::after{
                clear: both;
                content: "";
                display: table;
            }

            &--left {
                width: 50%;
                height: 100%;
                float: left;
                shape-outside: polygon(0 0, 100% 0, 60% 130%, 0 100%);
                overflow: hidden;
                
                position: relative;
                z-index: 2;


                @include respond-phone(tab-land) {
                    shape-outside: polygon(0 0, 100% 0, 50% 120%, 0 100%);
                }

                @include respond-phone(tab-port) {
                    shape-outside: polygon(0 0, 100% 0, 40% 130%, 0 100%);
                }

                @include respond-phone(phone) {
                    shape-outside: polygon(0 0, 110% 0, 0 130%);
                }


                .image-container {
                    height: 20rem;
                    width: 20rem;
                    clip-path: circle(50% at 50% 50%);
                    margin-left: 6rem;
                    margin-top: 5rem;
                    border-radius: 50%;
                    opacity: 0;
                    animation: show 1s forwards 3s ease-in-out;
                    

                    // outline: 3px solid $color-black;
                    overflow: hidden;

                    @include respond-phone(tab-land) {
                        height: 20rem;
                        margin-left: 4rem;
                        width: 20rem;
                    }

                    @include respond-phone(tab-port) {
                        height: 20rem;
                        margin-left: 4rem;
                        width: 20rem;
                    }

                   

                    @include respond-phone(phone) {
                        height: 15rem;
                        margin-left: 4rem;
                        width: 15rem;
                    }

                    .profile {
                        width: 100%;
                        object-fit: cover;
                        background-position: top;
                        transition: transform 0.2s;
                        transform: scale(1);
                    }

                    &:hover .profile {
                        transform: scale(1.2);
                    }
                }

               

                
            }

            &--right {
                padding: 3rem;

                &-flow {
                    .paragraph {
                        font-size: 1.2rem;

                     
                    }
                    opacity: 0;
                    animation: fadeIn .5s 4s ease-in forwards;
                }
                
            }
        }
    }
}
