.navigation {
    &__checkbox {
        display: none;
    }

    &__button {
        background-color: $color-white;
        height: 7rem;
        width: 7rem;
        position: fixed;
        top: 6rem;
        right: 6rem;
        border-radius: 50%;
        z-index: 12;
        box-shadow: 0 1rem 3rem rgba($color-black, 0.3);
        text-align: center;
        cursor: pointer;  
       
        @include respond-phone(tab-port) {
            top: 6rem;
            right: 6rem;
        }

        @include respond-phone(phone) {
            top: 4.5rem;
            right: 4.5rem;
        }

    }

    &__background {
        height: 6rem;
        width: 6rem;
        border-radius: 50%;
        position: fixed;
        top: 6.5rem;
        right: 6.5rem;
        z-index: 10;
        // transform: scale(80);
        transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);
        background-image: radial-gradient($primary-color-light, $primary-color-dark);

        @include respond-phone(tab-port) {
            top: 6.5rem;
            right: 6.5rem;
        }

        @include respond-phone(phone) {
            top: 5rem;
            right: 5rem;
        }
    }

    &__nav {
        height: 100vh;
        width: 100vw;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 11;
        opacity: 0;
        transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
        visibility: hidden;
    }

    &__list {
        @include absCenter;
        list-style: none;
        text-align: center;
    }

    &__item {
        margin: 1rem;
    }

    &__link {
        &:link,
        &:visited {
            display: inline-block;
            font-size: 3rem;
            font-weight: 300;
            padding: 1rem 2rem;
            color: $color-white;
            text-decoration: none;
            text-transform: uppercase;
            background-image: linear-gradient(120deg, transparent 0%, transparent 50%, $color-white 50%);
            background-size: 230%;
            transition: all 0.4s;
            transform: translateX(0);
        }

        &:hover,
        &:active {
            background-position: 100%;
            color: $primary-color;
            transform: translateX(-1rem);
        }
    }

    //Functionality
    &__checkbox:checked ~ &__background {
        transform: scale(80);
    }

    &__checkbox:checked ~ &__nav {
        opacity: 1;
        visibility: visible;
    }

    //ICON
    &__icon {
        position: relative;
        margin-top: 3.5rem;

        &,
        &::before,
        &::after {
            width: 3rem;
            height: 2px;
            background-color: $color-gray-dark;
            display: inline-block;

            transition: all 0.2s;
        }

        &::before,
        &::after {
            content: "";
            position: absolute;
        }

        &::before {
            left: 0;
            top: -0.8rem;
        }

        &::after {
            top: 0.8rem;
            left: 0;
        }
    }

    &__button:hover &__icon::before {
        top: -1rem;
    }

    &__button:hover &__icon::after {
        top: 1rem;
    }

    &__checkbox:checked + &__button > &__icon {
        background: transparent;

        &::before {
            top: 0;
            transform: rotate(135deg);
        }

        &::after {
            top: 0;
            transform: rotate(-135deg);
        }
    }
}
